Object.defineProperty(exports, "__esModule", { value: true });
var textIndex_1 = require("../translation/textIndex");
var textMenu_1 = require("../translation/textMenu");
var public_1 = require("../translation/public");
var textOurStory_1 = require("../translation/textOurStory");
var projects_1 = require("../translation/projects");
var project_1_1 = require("../translation/project-1");
var project_2_1 = require("../translation/project-2");
var project_3_1 = require("../translation/project-3");
var project_4_1 = require("../translation/project-4");
var project_5_1 = require("../translation/project-5");
